<template>
	<view class="wrap">
		<!-- 用户登入 -->
		<view class="uni-form-item uni-column" v-if="active === 1">
			<view class="title">用户登录</view>
			<view class="ipt">
				<text>账号</text>
				<input class="uni-input" name="input" placeholder="请输入账号.." v-model="phone" />
			</view>
			<view class="ipt">
				<text>密码</text>
				<input class="uni-input" name="input" placeholder="请输入密码.." v-model="pwd" />
			</view>
			<view class="btn">
				<button size="mini" @click="login">登录</button>
				<button size="mini" @click="active = 3">注册</button>
			</view>
		</view>
		<!-- 个人信息 -->
		<view class="my" v-if="active === 2">
			<view class="top">
				<image src="https://m.21cake.com/wap_themes/21cake/images/icon/ico_member.png"></image>
				<view>夏天的风</view>
				<view>手机:155555555555</view>
			</view>
		</view>
		<!-- 用户注册 -->
		<view class="uni-form-item uni-column" v-if="active === 3">
			<view class="title">用户注册</view>
			<view class="ipt">
				<text>账号</text>
				<input class="uni-input" name="input" placeholder="请输入账号.." v-model="phone" />
			</view>
			<view class="ipt">
				<text>密码</text>
				<input class="uni-input" name="input" placeholder="请输入密码.." v-model="pwd" />
			</view>
			<view class="ipt">
				<text>昵称</text>
				<input class="uni-input" name="input" placeholder="请输入密码.." v-model="nickname" />
			</view>
			<view class="btn">
				<button size="mini" @click="reg">注册</button>
				<!-- <button size="mini">确认</button>
				<button size="mini">返回</button> -->
			</view>
		</view>
	</view>
</template>

<script>
export default {
	data() {
		return {
			// 用户账号
			phone: '',
			// y用户密码
			pwd: '',
			// 用户名称
			nickname: '',
			// 控制是哪个页面显示
			active: 3
		};
	},
	// 监听页面加载
	onLoad() {

	},
	methods: {
		reg() {
			// 点击时候注册账户/
			var data = {
				// 用户账号 （必填）
				phone: this.phone,
				// 用户密码 （必填）
				pwd: this.pwd,
				// 用户昵称 （必填）
				nickname: this.nickname
			};
			uni.request({
				url: 'http://localhost:3000/user/reg', //仅为示例，并非真实接口地址。
				method: 'POST',
				data,
				success: res => {
					if (res.data.code === '10001') {
						uni.showModal({
							title: '登入成功',
							content: res.data.msg,
							showCancel: false
						});
					} else {
						uni.showModal({
							title: '账户已存在',
							content: res.data.msg,
							showCancel: false,
							complete: () => {
								this.active = 1;
							}
						});
					}
				}
			});
		}
	}
};
</script>

<style lang="scss">
.wrap {
	color: #b2b2b2;
	.ipt {
		background: #fff;
		width: 90%;
		margin: 0 auto;
		text {
			vertical-align: 20rpx;
		}
		input {
			display: inline-block;
			border-bottom: 1px solid #b2b2b2;
		}
		.btn {
			text-align: center;
		}
	}
	.my {
		border: 1px solid #8e6a55;
		border-radius: 4rpx;
		width: 98%;
		margin: 0 auto;
		.top {
			text-align: center;
			image {
				border-radius: 50%;
				width: 100rpx;
				height: 100rpx;
				border: 1px solid #8e6a55;
				display: inline-block;
			}
		}
		.bottom {
			display: flex;
			view {
				flex: 1;
				height: 50rpx;
				border: 1px solid #8e6a55;
			}
		}
	}
}
</style>
